import React, { useEffect, useState } from "react";
import { Box, Stack } from "@chakra-ui/react";
import Card from "./Card";
import axios from "axios";

const Home = () => {
  const kryptonPayApiKey =
    "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJhcHBJZCI6ImtwLXRlc3RfYXBwLTE3MzAxMzE0OTkwNjctYXBwIiwiaWF0IjoxNzMwMTMxNDk5LCJleHAiOjE3NjE2Njc0OTl9.-idxTFrTh0aBDw5seD1ZyIeUIBox9yRT0IYlWi3pf3g";
  const [razorKeyId, setRazorKeyId] = useState("");

  const kryptonPayURI = "http://13.127.170.65:8000";

  useEffect(() => {
    const fetchRazorpayKeyId = async () => {
      try {
        const { data: refreshMessage } = await axios.post(
          `${kryptonPayURI}/api/v1/app/generate-refresh-token`,
          {},
          {
            headers: {
              "kp-api-key": kryptonPayApiKey,
            },
            withCredentials: true,
          }
        );

        console.log("Refresh Message:", refreshMessage);

        if (refreshMessage) {
          const { data } = await axios.post(
            `${kryptonPayURI}/api/v1/app/razorpay-key-id`,
            {},
            {
              headers: {
                "kp-api-key": kryptonPayApiKey,
              },
            }
          );
          setRazorKeyId(data.data.key);
        }
      } catch (error) {
        console.error("Error fetching Razorpay Key ID:", error);
      }
    };

    fetchRazorpayKeyId();
  }, [kryptonPayApiKey]);

  const checkoutHandler = async (amount) => {
    try {
      const { data: createdOrder } = await axios.post(
        `${kryptonPayURI}/api/v1/payments/create`,
        { amount, currency: "INR" },
        {
          headers: {
            "kp-api-key": kryptonPayApiKey,
          },
        }
      );

      const order = createdOrder.data.razorpayOrder;

      const options = {
        key: razorKeyId,
        amount: order.amount,
        currency: "INR",
        name: "KryptonPay",
        description: "KryptonPay Test Transaction",
        image:
          "https://kryptonlab.tech/assets/img/Blue_Modern_Technology_and_Software_Company_Logo__2_-removebg-preview.png",
        order_id: order.id,
        handler: async function (response) {
          console.log("Payment Response:", response);

          if (response.razorpay_payment_id && response.razorpay_order_id) {
            const { data } = await axios.post(
              `${kryptonPayURI}/api/v1/payments/verify`,
              {
                razorpay_payment_id: response.razorpay_payment_id,
                razorpay_order_id: order.id,
                razorpay_signature: response.razorpay_signature,
                amount: order.amount,
                currency: "INR",
              },
              {
                headers: {
                  "kp-api-key": kryptonPayApiKey,
                },
              }
            );

            console.log("Verification Response:", data);
          } else {
            console.error("Payment response missing required information.");
          }
        },
        prefill: {
          name: "Gaurav Kumar",
          email: "gaurav.kumar@example.com",
          contact: "9999999999",
        },
        notes: {
          address: "Razorpay Corporate Office",
        },
        theme: {
          color: "#121212",
        },
      };

      const razor = new window.Razorpay(options);
      razor.open();

      razor.on("payment.failed", async (paymentData) => {
        console.log("Payment failed:", paymentData);
      });

      razor.on("payment.error", (error) => {
        console.error("Payment error:", error);
        alert(
          "An error occurred while processing the payment. Please try again."
        );
      });
    } catch (error) {
      console.error("Payment processing error:", error);
      alert(
        "An error occurred while processing the payment. Please try again."
      );
    }
  };

  return (
    <Box>
      <Stack
        h={"100vh"}
        alignItems="center"
        justifyContent="center"
        direction={["column", "row"]}
      >
        <Card
          amount={5000}
          img={
            "https://5.imimg.com/data5/SELLER/Default/2023/3/293121145/AY/XN/YG/186342913/apple-laptops.png"
          }
          checkoutHandler={checkoutHandler}
        />
        <Card
          amount={3000}
          img={
            "http://i1.adis.ws/i/canon/eos-r5_front_rf24-105mmf4lisusm_32c26ad194234d42b3cd9e582a21c99b"
          }
          checkoutHandler={checkoutHandler}
        />
      </Stack>
    </Box>
  );
};

export default Home;
